@import url(https://fonts.googleapis.com/css?family=Kaushan+Script&display=swap);
:root {
  --aside-size: 23rem;
  --cover-font-s: 16px;
  --cover-font-c: #fefefe;
  --cover-w: 300px;
  --cover-h: 300px;
  --cover-bg: #de4f45;
  --cover-pd: 30px; }

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

html,
body,
#root {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background-color: #2b2b2b; }

.wrapper {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template: 100%/23rem 1fr;
  grid-template: 100%/var(--aside-size) 1fr;
  grid-column-gap: 10px;
  -webkit-column-gap: 10px;
     -moz-column-gap: 10px;
          column-gap: 10px; }

.aside {
  overflow-y: auto;
  background-color: #fefefe;
  box-shadow: 0 0 1px 0 rgba(21, 21, 21, 0.3); }
  .aside__form {
    display: flex;
    flex-flow: column;
    justify-items: center;
    padding: 10px; }
    .aside__form__select {
      margin-top: 25px !important; }
  .aside__label {
    font-family: 'Roboto';
    font-weight: 200;
    font-size: 18px;
    text-transform: capitalize;
    margin: 20px 0 10px 5px; }
  .aside__input {
    font-size: 16px;
    border: none;
    font-family: 'Roboto'; }
  .aside__picker {
    display: flex;
    flex-flow: column;
    align-items: center; }
  .aside__list {
    max-height: 15rem;
    overflow-y: auto; }

.main {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--bg); }
  .main .cover {
    width: 300px;
    width: var(--cover-w);
    height: 300px;
    height: var(--cover-h);
    background-color: #de4f45;
    background-color: var(--cover-bg);
    box-shadow: 0 5px 10px -5px rgba(21, 21, 21, 0.6);
    -webkit-transition: background-color 0.1s 0s linear;
    transition: background-color 0.1s 0s linear;
    padding: 30px;
    padding: var(--cover-pd);
    position: relative;
    overflow: hidden; }
    .main .cover__wrapper {
      position: absolute;
      top: 0;
      left: 0;
      display: flex;
      justify-content: space-between;
      width: 100%;
      height: 100%;
      padding: inherit; }
    .main .cover__tunes {
      margin: calc((300px - var(--cover-art-s) - 30px) / 2) auto auto 30px;
      margin: calc((var(--cover-h) - var(--cover-art-s) - var(--cover-pd)) / 2) auto auto 30px;
      font-size: 16px;
      font-size: var(--cover-font-s);
      font-family: 'Roboto';
      color: #fefefe;
      color: var(--cover-font-c);
      font-weight: 200;
      line-height: 1.75;
      text-shadow: 0.2px 0.2px #fefefe, 1px 1px rgba(21, 21, 21, 0.4); }
    .main .cover__bg {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-left: calc(30px * -1);
      margin-left: calc(var(--cover-pd) * -1);
      margin-top: calc(30px * -1);
      margin-top: calc(var(--cover-pd) * -1);
      width: calc(300px*1.1);
      width: calc(var(--cover-w)*1.1);
      height: calc(300px*1.1);
      height: calc(var(--cover-h)*1.1);
      background-image: var(--cover-art);
      -webkit-filter: blur(13px) contrast(0.8) saturate(0.9);
              filter: blur(13px) contrast(0.8) saturate(0.9);
      -webkit-transform: translateX(-5%);
              transform: translateX(-5%);
      position: relative; }
      .main .cover__bg::after {
        content: "";
        display: block;
        position: absolute;
        left: 0;
        width: 100%;
        height: 100%;
        background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 255, 255, 0)), color-stop(75%, rgba(21, 21, 21, 0.85)));
        background-image: linear-gradient(rgba(255, 255, 255, 0), rgba(21, 21, 21, 0.85) 75%); }
    .main .cover__art {
      margin: auto;
      height: var(--cover-art-s);
      object-fit: fill;
      box-shadow: 0 0 5px 0px rgba(21, 21, 21, 0.3); }
    .main .cover__signature {
      position: absolute;
      top: calc(300px - 45px);
      top: calc(var(--cover-h) - 45px);
      right: 30px;
      right: var(--cover-pd);
      font-family: 'Kaushan Script', cursive;
      color: rgba(255, 255, 255, 0.4);
      font-size: 14px;
      z-index: 1; }

